import React, { useState, useRef, useEffect } from 'react';
import { useStyles } from 'react-styles-hook'
import { Form, Row, Col, Button, Container } from 'react-bootstrap';
import { PlusOutlined, EditFilled, CloseCircleFilled, PlusCircleFilled, PlusCircleOutlined } from '@ant-design/icons';
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";

import gbstyle from '../../assets/gbstyle'

export default function FrmDragDrop() {
    const [items, setItems] = useState([
        {
            Id: '1',
            Name: "Sea Freight (Import)",
            Documents: [
                { Id: '1', Name: "Invoice", State: "Edit" },
                { Id: '2', Name: "Package List", State: "Edit" },
                { Id: '3', Name: "B/L", State: "Edit" },
                { Id: '4', Name: "Privillage Form", State: "Edit" },
                { Id: '5', Name: "Invoice", State: "Edit" },
                { Id: '6', Name: "Package List", State: "Edit" },
                { Id: '7', Name: "B/L", State: "Edit" },
                { Id: '8', Name: "Privillage Form", State: "Edit" }
            ]
        },
        {
            Id: '2',
            Name: "Sea Freight (Export)",
            Documents: [
                { Id: '1', Name: "Invoice", State: "Edit" },
                { Id: '2', Name: "Package List", State: "Edit" },
                { Id: '3', Name: "B/L", State: "Edit" },
                { Id: '4', Name: "Privillage Form", State: "Edit" },
            ]
        },
        {
            Id: '3',
            Name: "Air Freight (Import)",
            Documents: [
                { Id: '1', Name: "Invoice", State: "Edit" },
                { Id: '2', Name: "Package List", State: "Edit" },
                { Id: '3', Name: "B/L", State: "Edit" },
                { Id: '4', Name: "Privillage Form", State: "Edit" },
            ]
        },
        {
            Id: '4',
            Name: "Air Freight (Export)",
            Documents: [
                { Id: '1', Name: "Invoice", State: "Edit" },
                { Id: '2', Name: "Package List", State: "Edit" },
                { Id: '3', Name: "B/L", State: "Edit" },
                { Id: '4', Name: "Privillage Form", State: "Edit" },
            ]
        },
        {
            Id: '5',
            Name: "Cross Border Truck (Import)",
            Documents: [
                { Id: '1', Name: "Invoice", State: "Edit" },
                { Id: '2', Name: "Package List", State: "Edit" },
                { Id: '3', Name: "B/L", State: "Edit" },
                { Id: '4', Name: "Privillage Form", State: "Edit" },
            ]
        },
        {
            Id: '6',
            Name: "Sea Freight (Export)",
            Documents: [
                { Id: '1', Name: "Invoice", State: "Edit" },
                { Id: '2', Name: "Package List", State: "Edit" },
                { Id: '3', Name: "B/L", State: "Edit" },
                { Id: '4', Name: "Privillage Form", State: "Edit" },
            ]
        },
        {
            Id: '7',
            Name: "Domestic Truck",
            Documents: [
                { Id: '1', Name: "Invoice", State: "Edit" },
                { Id: '2', Name: "Package List", State: "Edit" },
                { Id: '4', Name: "Privillage Form", State: "Edit" },
            ]
        },
        {
            Id: '8',
            Name: "Warehouse",
            Documents: [
                { Id: '1', Name: "Invoice", State: "Edit" },
                { Id: '3', Name: "B/L", State: "Edit" },
                { Id: '4', Name: "Privillage Form", State: "Edit" },
            ]
        }
    ]);

    useEffect(() => {
    }, []);

    useEffect(() => {
        console.log(items)
    }, [items]);

    const getItemStyle = (isDragging, draggableStyle, mystyle) => ({
        ...mystyle,
        ...draggableStyle
    });

    const getListStyle = isDraggingOver => ({
        background: isDraggingOver ? "lightblue" : "lightgrey"
    });

    const reorder = (list, startIndex, endIndex) => {
        const result = Array.from(list);
        const [removed] = result.splice(startIndex, 1);
        result.splice(endIndex, 0, removed);

        return result;
    };

    const findItem = (serviceid) => {
        let newitems = [...items];
        let item = newitems.filter(data => data.Id == serviceid)[0]
        return item
    }

    const makeid = (length) => {
        var result = '';
        var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        var charactersLength = characters.length;
        for (var i = 0; i < length; i++) {
            result += characters.charAt(Math.floor(Math.random() * charactersLength));
        }
        return result;
    }


    const btnAddDocClick = (serviceid) => {
        let newitems = [...items];
        let item = newitems.filter(data => data.Id == serviceid)[0]

        if (item.Documents.filter(data => data.State == 'Add').length > 0)
            return

        let id = makeid(5)
        let newItem = { Id: id, Name: "", State: "Add" }
        item.Documents.push(newItem)
        setItems(newitems);
    }

    const btnDelDocClick = (serviceid, document) => {
        let newitems = [...items];
        let item = newitems.filter(data => data.Id == serviceid)[0]
        if (item) {
            let documents = item.Documents.filter(data => data.Id != document.Id);
            item.Documents = documents
            setItems(newitems);
        }
    }

    const btnSaveDocClick = (serviceid, document) => {
        let newitems = [...items];
        let item = newitems.filter(data => data.Id == serviceid)[0]
        if (item) {
            let documents = item.Documents.filter(data => data.Id == document.Id)[0];
            documents.State = 'Edit'
            setItems(newitems);
        }
    }

    const docInputChange = (event, serviceid, document) => {
        let newitems = [...items];
        let item = newitems.filter(data => data.Id == serviceid)[0]
        if (item) {
            let documents = item.Documents.filter(data => data.Id == document.Id)[0];
            documents.Name = event.target.value
            setItems(newitems);
        }
    }


    function onDragEnd(result, id, documents) {
        //console.log('drag end')
        if (!result.destination) {
            return;
        }

        if (result.destination.index === result.source.index) {
            return;
        }

        const itemsorder = reorder(
            documents,
            result.source.index,
            result.destination.index
        )

        let newitems = [...items];
        let item = newitems.filter(data => data.Id == id)[0]
        if (item) {
            item.Documents = itemsorder;
            //console.log(newdatalist)
            setItems(newitems);
        }
    }

    const DivBody = (id, documents) => {
        return (
            <div id="divdragdropbody" style={{ ...styles.divmainbody }}>
                <DragDropContext onDragEnd={(result) => onDragEnd(result, id, documents)}>
                    <Droppable droppableId={id}>
                        {(provided, snapshot) => (
                            <div
                                {...provided.droppableProps}
                                ref={provided.innerRef}
                            >
                                {documents.filter(data => data.State == 'Edit').map((item, index) => (
                                    <Draggable key={item.Id} draggableId={item.Id} index={index}>
                                        {(provided, snapshot) => (
                                            <div>
                                                < div
                                                    className="btn btn-primary btn-sm"
                                                    ref={provided.innerRef}
                                                    {...provided.draggableProps}
                                                    {...provided.dragHandleProps}
                                                    style={getItemStyle(
                                                        snapshot.isDragging,
                                                        provided.draggableProps.style,
                                                        { ...gbstyle.formround, ...gbstyle.formbutton, ...gbstyle.formcontrol, fontSize: 12, width: '95%', marginTop: 5, display: "flex", justifyContent: "space-between", alignItems: "center" }
                                                    )}
                                                >
                                                    {' ' + item.Name}
                                                    <
                                                        CloseCircleFilled style={{ fontSize: 15 }} className="nofixtop" onClick={() => btnDelDocClick(id, item)}
                                                    />
                                                </div>
                                            </div>
                                        )}
                                    </Draggable>
                                ))}

                                {provided.placeholder}
                            </div>
                        )}
                    </Droppable>
                </DragDropContext >


                <Form>
                    {documents.filter(data => data.State == 'Add').map((item, index) => (
                        <Form.Group >
                            <Row className="btn btn-sm"
                                style={
                                    {
                                        ...gbstyle.formround, ...gbstyle.formbuttonoutlne, ...gbstyle.formcontrol, width: '95%', marginTop: 5, display: "flex", alignItems: "center", justifyContent: "space-between",
                                        backgroundColor: 'white', borderColor: '#CCCCCC', paddingTop: 0, paddingBottom: 0
                                    }
                                }>
                                <Form.Control
                                    size="sm" type="text"
                                    placeholder="Other Document(s)"
                                    style={{ maxHeight: 26, fontSize: 12, width: '70%', borderWidth: 0, backgroundColor: 'transparent' }}
                                    onChange={(event) => docInputChange(event, id, item)}
                                />

                                <
                                    EditFilled
                                    style={{ fontSize: 15, color: "#78BE9C" }}
                                    className="nofixtop"
                                    onClick={() => btnSaveDocClick(id, item)}
                                />

                                <div style={styles.verticalLine}>
                                </div>

                                <
                                    CloseCircleFilled
                                    style={{ fontSize: 15, color: "#78BE9C"}}
                                    className="nofixtop"
                                    onClick={() => btnDelDocClick(id, item)}
                                />
                            </Row>
                        </Form.Group>
                    ))}
                </Form>
            </div>
        )
    }

    const DivMain = (item) => {
        //console.log(rowData)
        return (
            <div style={styles.divmain}>
                <div style={styles.divmainheader}>
                    {item.Name}
                </div>

                {DivBody(item.Id, item.Documents)}

                <div style={styles.divmainfooter}>
                    <Button
                        size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbuttonoutline, ...gbstyle.formcontrol, fontSize: 12, width: '95%' }} className="formbutton"
                        onClick={() => btnAddDocClick(item.Id)}
                    >
                        <PlusCircleFilled style={{ fontSize: 15, marginLeft: 0 }} />
                        {' '}Add New Document
                    </Button>
                </div>
            </div>
        );
    }

    return (
        <div>
            <Form>
                <Form.Group >
                    <Row >
                        <Col style={gbstyle.formtoppic}>
                            REQUIRED DOCUMENT
                        </Col>
                    </Row>
                    <hr className="formhr" />
                    <Row >
                        {items.map((item, index) => (
                            <Col key={index} md={3} style={gbstyle.formtoppic}>
                                {DivMain(item)}
                            </Col>
                        ))}
                    </Row>
                </Form.Group>
            </Form>
        </div>
    )
}

const styles = useStyles({
    divmain: {
        height: 300,
        marginTop: 15,
        borderRadius: 10,
        padding: 8,
        borderStyle: "solid",
        borderColor: '#CACACA',
        borderWidth: 1,
        boxShadow: "2px 2px 2px #E8E8E8"
    },
    divmainheader: {
        height: '8%',
        fontStyle: 'bold',
        fontSize: 10,
        borderBottomWidth: 1,
        borderBottomColor: '#C0C0C0',
        borderBottomStyle: "solid",
        display: "flex",
        justifyContent: "center",
        alignContent: "center",
        marginBottom: 5
    },
    divmainbody: {
        height: '78%',
        display: "flex",
        flexDirection: "column",
        justifyContent: "flex-start",
        alignContent: "center",
        overflowY: "auto"
    },
    divmainfooter: {
        height: '10%',
        display: "flex",
        justifyContent: "center",
        alignContent: "center",
        marginTop: 5
    },
    verticalLine: {
        borderLeft: '1px solid #78BE9C',
        height: 20
    }
})




